import React, { useState, useEffect } from 'react';
import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton, IconButton, Badge, styled } from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import img1 from '../../asserts/Kitchen/3084_commercial_gas_range_single_burner_ss_304-305x305.webp';
import img2 from '../../asserts/Kitchen/3097_chinese_cooking_range-305x305.webp';
import img3 from '../../asserts/Kitchen/437_double_burner_range_stainless_steel-305x305.webp';
import img4 from '../../asserts/Kitchen/502_single_burner_square_18x18-500x500.webp';
import img5 from '../../asserts/Kitchen/572_table_top_single_burner_12-305x305.webp';
import img6 from '../../asserts/Kitchen/H0f98ff0fd4b14713bd869ac7a2079d1bU.jpg_720x720q50.avif';
import img7 from '../../asserts/Kitchen/Stock_Pot_Ranges.webp';
import img8 from '../../asserts/Kitchen/images.jpeg';



// Sample product data for Kitchen Equipment
const products = [
  {
    name: "Commercial Gas Range",
    imageUrl: img1,
    description: "High-performance commercial gas range for professional kitchens.",
    price: "$899.99",
  },
  {
    name: "Commercial Electric Range",
    imageUrl: img2,
    description: "Efficient electric range suitable for fast-paced kitchens.",
    price: "$799.99",
  },
  {
    name: "Commercial Induction Cooktop",
    imageUrl: img3,
    description: "State-of-the-art induction cooktop for precise temperature control.",
    price: "$1,199.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img4,
    description: "Fast and efficient infrared cooktop for any kitchen.",
    price: "$999.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img5,
    description: "Fast and efficient infrared cooktop for any kitchen.",
    price: "$999.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img6,
    description: "Fast and efficient infrared cooktop for any kitchen.",
    price: "$999.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img7,
    description: "Fast and efficient infrared cooktop for any kitchen.",
    price: "$999.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img8,
    description: "Fast and efficient infrared cooktop for any kitchen.",
    price: "$999.99",
  },
];

function Commercial_Gas() {
  const [loading, setLoading] = useState(true);

  // Simulate loading effect
  useEffect(() => {
    const timer = setTimeout(() => setLoading(false), 1000); // Simulate loading for 1.5 seconds
    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="kitchen-container" style={{ padding: '20px' }}>
      <Typography variant="h3" gutterBottom align="center" style={{ color: '#333' }}>
        Gas Range
      </Typography>

      {/* Sticky Cart Icon */}
      <IconButton
        aria-label="cart"
        style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          zIndex: 9999,
        }}
      >
        
      </IconButton>

      {/* Product Grid */}
      <Grid container spacing={4} justifyContent="center">
        {products.map((product, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card
              sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
                borderRadius: '10px',
                boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
                transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
                border: '2px solid #ccc',
                '&:hover': {
                  transform: 'scale(1.05)',
                  borderColor: '#007BFF',
                },
              }}
            >
              {/* Image Skeleton Loader */}
              {loading ? (
                <Skeleton variant="rectangular" width="100%" height={180} />
              ) : (
                <CardMedia
                  component="img"
                  height="180"
                  image={product.imageUrl}
                  alt={product.name}
                  sx={{ objectFit: 'contain' }}
                />
              )}

              <CardContent>
                {/* Text Skeleton Loader */}
                {loading ? (
                  <>
                    <Skeleton variant="text" width="80%" />
                    <Skeleton variant="text" width="60%" />
                    <Skeleton variant="text" width="40%" />
                  </>
                ) : (
                  <>
                    <Typography
                      variant="h6"
                      component="div"
                      sx={{
                        textAlign: 'center',
                        padding: 1,
                        color: '#555',
                        fontWeight: 'bold',
                      }}
                    >
                      {product.name}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" paragraph>
                      {product.description}
                    </Typography>
                    <Typography variant="h6" color="primary">
                      {product.price}
                    </Typography>
                  </>
                )}

                {/* Add to Cart Button with Outlined Variant */}
                <Button variant="outlined" color="primary" fullWidth style={{ marginTop: '10px' }}>
                  {loading ? <Skeleton variant="text" width="60%" /> : "Add to Cart"}
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default Commercial_Gas;
