import React, { useState, useEffect } from 'react';
import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton } from '@mui/material';
import img1 from '../../asserts/Kitchen/Inducation/1730980691039-watmrkA.webp'
import img2 from '../../asserts/Kitchen/Inducation/2394_Induction_Hot_plate_5000w-228x228.jpg'
import img3 from '../../asserts/Kitchen/Inducation/drop-in_induction_wok_3500_w-268x299.webp'
import img4 from '../../asserts/Kitchen/Inducation/froth-flavor-froth-flavor-commercial-induction-cooktop-852369-original-imafjpbgnvgbjyn2.webp'
import img5 from '../../asserts/Kitchen/Inducation/induction_wok_sirman_ih-35-wok-268x299.webp'
import img6 from '../../asserts/Kitchen/Inducation/table_top_induction_hob_3500_w-268x299.webp'

const products = [
  {
    name: "Infrared Cooktop",
    imageUrl: img1,
    description: "Advanced infrared cooktop offering fast and efficient heating for professional kitchens.",
    price: "$499.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img2,
    description: "Advanced infrared cooktop offering fast and efficient heating for professional kitchens.",
    price: "$499.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img3,
    description: "Advanced infrared cooktop offering fast and efficient heating for professional kitchens.",
    price: "$499.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img4,
    description: "Advanced infrared cooktop offering fast and efficient heating for professional kitchens.",
    price: "$499.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img5,
    description: "Advanced infrared cooktop offering fast and efficient heating for professional kitchens.",
    price: "$499.99",
  },
  {
    name: "Infrared Cooktop",
    imageUrl: img6,
    description: "Advanced infrared cooktop offering fast and efficient heating for professional kitchens.",
    price: "$499.99",
  }
];

function Commerical_induction() {
  const [loading, setLoading] = useState(true);

  // Simulate loading effect
  useEffect(() => {
    const timer = setTimeout(() => setLoading(false), 1000); // Loading for 1.5s
    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="kitchen-container" style={{ padding: '20px' }}>
      <Typography variant="h3" gutterBottom align="center" style={{ color: '#333' }}>
        Commercial Induction
      </Typography>
      <Grid container spacing={4} justifyContent="center">
        {products.map((product, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card sx={{
              height: '100%',
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'space-between',
              borderRadius: '10px',
              boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.1)',
              transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
              border: '2px solid #ccc',
              '&:hover': {
                transform: 'scale(1.05)',
                borderColor: '#007BFF',
              }
            }}>
              {loading ? (
                <Skeleton variant="rectangular" width="100%" height={180} />
              ) : (
                <CardMedia
                  component="img"
                  height="180"
                  src={product.imageUrl}
                  alt={product.name}
                  sx={{ objectFit: 'contain' }}
                />
              )}
              <CardContent>
                {loading ? (
                  <>
                    <Skeleton variant="text" width="80%" />
                    <Skeleton variant="text" width="60%" />
                    <Skeleton variant="text" width="40%" />
                  </>
                ) : (
                  <>
                    <Typography variant="h6" component="div">
                      {product.name}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" paragraph>
                      {product.description}
                    </Typography>
                    <Typography variant="h6" color="primary">
                      {product.price}
                    </Typography>
                  </>
                )}
                <Button variant="outlined" color="primary" fullWidth style={{ marginTop: '10px' }}>
                  {loading ? <Skeleton variant="text" width="60%" /> : "Add to Cart"}
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default Commerical_induction;
