import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton } from '@mui/material';
import React, { useState, useEffect } from 'react';
import img1 from '../../asserts/Food/Milk Steamer/616ZfRpBxhL._SL1500.webp';
import img2 from '../../asserts/Food/Milk Steamer/71Vyx3t9euL.jpg';
import img3 from '../../asserts/Food/Milk Steamer/71aO0anwcqL._AC_UF894,1000_QL80_.jpg';
import img4 from '../../asserts/Food/Milk Steamer/artboard_2_1_.png';
import img5 from '../../asserts/Food/Milk Steamer/images.jpeg';

// Sample Products data for MilkSteamer
const Products = [
  {
    name: 'Milk Steamer 1',
    imageUrl: img1,
    description: 'Efficient milk steamer for quick frothing of milk.',
    price: '$1000',
  },
  {
    name: 'Milk Steamer 2',
    imageUrl: img2,
    description: 'Compact milk steamer for small-scale operations.',
    price: '$1000',
  },
  {
    name: 'Milk Steamer 3',
    imageUrl: img3,
    description: 'Professional-grade milk steamer with rapid heating.',
    price: '$1000',
  },
  {
    name: 'Milk Steamer 4',
    imageUrl: img4,
    description: 'Sleek and modern milk steamer with easy-to-use controls.',
    price: '$1000',
  },
  {
    name: 'Milk Steamer 5',
    imageUrl: img4,
    description: 'High-efficiency milk steamer designed for commercial use.',
    price: '$1000',
  },
  {
    name: 'Milk Steamer 6',
    imageUrl: img5,
    description: 'Durable milk steamer for cafes and restaurants.',
    price: '$1000',
  },
];

function MilkSteamer() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false); // Simulate loading delay
    }, 1000); // 2 seconds delay

    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="food-preparation">
      <Typography variant="h3" gutterBottom>
        Food Preparation
      </Typography>

      {/* Sticky Cart Icon */}
      <div
        style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          backgroundColor: '#007BFF',
          color: 'white',
          padding: '10px',
          borderRadius: '50%',
          boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
        }}
      >
        
      </div>

      <Grid container spacing={4} justifyContent="center">
        {Products.map((product, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card
              sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
                borderRadius: '10px',
                boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
                transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
                border: '2px solid #ccc',
                '&:hover': {
                  transform: 'scale(1.05)',
                  borderColor: '#007BFF',
                },
              }}
            >
              {loading ? (
                <Skeleton variant="rectangular" width="100%" height={180} />
              ) : (
                <CardMedia
                  component="img"
                  height="180"
                  image={product.imageUrl}
                  alt={product.name}
                  sx={{ objectFit: 'contain' }}
                />
              )}
              <CardContent>
                {loading ? (
                  <Skeleton variant="text" width="80%" />
                ) : (
                  <Typography
                    variant="h6"
                    component="div"
                    sx={{
                      textAlign: 'center',
                      padding: 1,
                      color: '#555',
                      fontWeight: 'bold',
                    }}
                  >
                    {product.name}
                  </Typography>
                )}

                {loading ? (
                  <Skeleton variant="text" width="60%" />
                ) : (
                  <Typography variant="body2" color="text.secondary" paragraph>
                    {product.description}
                  </Typography>
                )}

                {loading ? (
                  <Skeleton variant="text" width="40%" />
                ) : (
                  <Typography variant="h6" color="primary" sx={{ textAlign: 'center' }}>
                    {product.price}
                  </Typography>
                )}

                <Button variant="outlined" color="primary" fullWidth>
                  {loading ? <Skeleton width="100%" height={36} /> : 'Add to Cart'}
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default MilkSteamer;
