import React, { useState, useEffect } from 'react';
import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton } from '@mui/material';
import img1 from '../../asserts/Bakery/Deep Fryer/633ad2f364c21f45d211ce14-topkitch-electric-deep-fryer-stainless.jpg';
import img2 from '../../asserts/Bakery/Deep Fryer/images.jpeg';
import img3 from '../../asserts/Bakery/Deep Fryer/images (1).jpeg';
import img4 from '../../asserts/Bakery/Deep Fryer/en-01.webp';
import img5 from '../../asserts/Bakery/Deep Fryer/Vn8lCadIlVE7FvT3AFMPo8Kd1lpsOmSxzIbYvSWp.webp';

// Sample product data for Deepfry
const products = [
    {
        name: "Deep Fry",
        imageUrl: img1,
        description: "",
        price: "Rs:2500"
    },
    {
        name: "Electric Deep Fry",
        imageUrl: img2,
        description: "",
        price: "Rs:3000"
    }, 
    {
        name: "Mini Electric Deep Fry",
        imageUrl: img3,
        description: "",
        price: "Rs:2000"
    }, 
    {
        name: "Deep Fryer 4ltr Electric Single Tank Indulge",
        imageUrl: img4,
        description: "",
        price: "Rs:2000"
    }, 
    {
        name: "Deep Fryer 20Ltr Gas Dean",
        imageUrl: img5,
        description: "",
        price: "Rs:2000"
    },
];

function DeepFry() {
    const [loading, setLoading] = useState(true);

    // Simulate loading state for demonstration
    useEffect(() => {
        const timer = setTimeout(() => {
            setLoading(false); // Stop loading after 2 seconds
        }, 1000);

        return () => clearTimeout(timer); // Cleanup the timer
    }, []);

    return (
        <div className="food-preparation">
            <Typography variant="h3" gutterBottom>
                Bakery Equipment
            </Typography>

            <Grid container spacing={4} justifyContent="center">
                {products.map((product, index) => (
                    <Grid item xs={12} sm={6} md={3} key={index}>
                        <Card
                            sx={{
                                height: '100%',
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'space-between',
                                borderRadius: '10px',
                                boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
                                transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
                                border: '2px solid #ccc',
                                '&:hover': {
                                    transform: 'scale(1.05)',
                                    borderColor: '#007BFF',
                                },
                            }}
                        >
                            {/* Skeleton Loader for image */}
                            {loading ? (
                                <Skeleton variant="rectangular" width="100%" height={180} />
                            ) : (
                                <CardMedia
                                    component="img"
                                    height="180"
                                    image={product.imageUrl}
                                    alt={product.name}
                                    sx={{ objectFit: 'contain' }}
                                />
                            )}

                            <CardContent>
                                {/* Skeleton Loader for name */}
                                {loading ? (
                                    <Skeleton variant="text" width="80%" />
                                ) : (
                                    <Typography variant="h6" component="div">
                                        {product.name}
                                    </Typography>
                                )}

                                {/* Skeleton Loader for description */}
                                {loading ? (
                                    <Skeleton variant="text" width="60%" />
                                ) : (
                                    <Typography variant="body2" color="text.secondary">
                                        {product.description}
                                    </Typography>
                                )}

                                {/* Skeleton Loader for price */}
                                {loading ? (
                                    <Skeleton variant="text" width="40%" />
                                ) : (
                                    <Typography variant="h6" component="div">
                                        {product.price}
                                    </Typography>
                                )}

                                {/* Skeleton Loader for button */}
                                {loading ? (
                                    <Skeleton variant="rectangular" width="100%" height={36} />
                                ) : (
                                    <Button variant="outlined" color="primary" fullWidth>
                                        Add to Cart
                                    </Button>
                                )}
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </div>
    );
}

export default DeepFry;
