import React, { useState } from 'react';
import { AppBar, Toolbar, Typography, Button, Container, IconButton, InputBase, Box, Stack, Menu, MenuItem } from '@mui/material';
import { Link } from 'react-router-dom';
import SearchIcon from '@mui/icons-material/Search';
import PhoneIcon from '@mui/icons-material/Phone';
import HomeIcon from '@mui/icons-material/Home';

const categories = [
  {
    name: "Kitchen Equipment",
    items: ["Commercial Gas Range", "Commercial Electric Range", "Commercial Induction Cooktop", "Infrared Cooktop"],
  },
  {
    name: "Food Preparation",
    items: ["Idli Steamer", "Dhokla Steamer", "Commercial Food Steamer", "Milk Steamer"],
  },
  {
    name: "Bakery Equipment",
    items: ["Deep Fryer", "Donut Fryer", "Pressure Fryer"],
  },
  {
    name: "Chicken Barbecue Machine",
    items: ["Grill Chicken Machine", "Shawarma Machine", "Alfaham Machine", "Barbeque Machine"],
  },
];

const Navbar = ({ onSearchChange }) => {
  const [searchTerm, setSearchTerm] = useState('');
  const [anchorEl, setAnchorEl] = useState(null);
  const [selectedCategory, setSelectedCategory] = useState(null);

  const handleSearchChange = (event) => {
    const newSearchTerm = event.target.value;
    setSearchTerm(newSearchTerm);
    onSearchChange(newSearchTerm);
  };

  const submitSearch = () => {
    console.log('Search submitted:', searchTerm);
  };

  const handleClick = (event, category) => {
    setAnchorEl(event.currentTarget);
    setSelectedCategory(category);
  };

  const handleClose = () => {
    setAnchorEl(null);
    setSelectedCategory(null);
  };

  return (
    <>

      {/* Existing Navbar */}
      <AppBar
        position="sticky"
        sx={{
          top: 0,
          background: 'linear-gradient(90deg, #004e92, #000428)',
          boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.2)',
          padding: '2px 0',
          zIndex: 1100 // Ensures it stays on top
        }}
      >
        <Toolbar>
        <Box sx={{ display: "flex", alignItems: "center" }}>
          {/* Home Icon to Redirect to '/' */}
          <IconButton
            component={Link}
            to="/"
            sx={{ color: 'yellow', marginRight: 2 }} // Add some spacing to the right of the icon
          >
            <HomeIcon />
          </IconButton>
        </Box>
          <Container
            sx={{
              display: 'flex',
              flexDirection: { xs: 'column', sm: 'row' },
              alignItems: 'center',
              justifyContent: 'space-between',
              width: '100%',
            }}
          >
            {/* Search Bar */}
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                width: { xs: '100%', sm: 'auto' },
                mb: { xs: 1, sm: 0 },
                backgroundColor: 'rgba(255,255,255,0.2)',
                borderRadius: '20px',
                padding: '5px 10px',
              }}
            >
              <InputBase
                placeholder="Search..."
                inputProps={{ 'aria-label': 'search' }}
                sx={{
                  color: 'white',
                  flexGrow: 1,
                  width: { xs: '100%', sm: 'auto' },
                }}
                value={searchTerm}
                onChange={handleSearchChange}
              />
              <IconButton onClick={submitSearch} sx={{ color: 'yellow' }}>
                <SearchIcon />
              </IconButton>
            </Box>

            {/* Company Name */}
            <Typography
              variant="h4"
              component="h1"
              sx={{
                fontWeight: 'bold',
                color: 'white',
                textTransform: 'uppercase',
                textAlign: 'center',
                fontFamily: "'Poppins', sans-serif",
                letterSpacing: '2px'
              }}
            >
              Krishna Industries
            </Typography>

            {/* Contact Info */}
            <Stack
              direction="row"
              alignItems="center"
              spacing={2}
              sx={{ width: { xs: '100%', sm: 'auto' }, textAlign: 'center' }}
            >
              <Box sx={{ display: 'flex', alignItems: 'center', color: 'white' }}>
                <PhoneIcon sx={{ mr: 1, color: 'yellow' }} />
                <Typography variant="body1" sx={{ fontWeight: 'bold' }}>
                  <a href='tel:+919171585789' style={{ textDecoration: 'none', color: 'inherit' }}>91715 85789</a>
                </Typography>
              </Box>
            </Stack>
          </Container>
        </Toolbar>
      </AppBar>



      {/* Dropdown Menu Section */}
      <AppBar
        position="sticky"
        sx={{
          background: 'linear-gradient(90deg, #004e92, #000428)',
          marginTop: 0,
          zIndex: 1000
        }}
      >
        {/* <Toolbar>
          <IconButton
          component={Link}
          to="/"
          sx={{ color: 'yellow', marginRight: 0 }}
          >
            <HomeIcon />
          </IconButton>
        </Toolbar> */}
        
        <Toolbar> 
          <Container sx={{ display: "flex", justifyContent: "center" }}>

            {/* Dropdown Menus */}
            {categories.map((category, index) => (
              <div key={index}>
                <Button
                  color="inherit"
                  onMouseEnter={(event) => handleClick(event, category)}
                  sx={{ textTransform: "none", fontSize: "16px", mx: 1 }}
                >
                  {category.name}
                </Button>

                <Menu
                  anchorEl={anchorEl}
                  open={Boolean(anchorEl) && selectedCategory === category}
                  onClose={handleClose}
                  MenuListProps={{ onMouseLeave: handleClose }}
                  sx={{ marginTop: "15px" }}  // Moves dropdown slightly lower
                >
                  {category.items.map((item, i) => (
                    <MenuItem key={i} onClick={handleClose}>
                      <Link
                        to={
                          item === 'Commercial Gas Range' ? '/Commerical_Gas' :
                            item === 'Commercial Electric Range' ? '/Commerical_Electric' :
                              item === 'Commercial Induction Cooktop' ? '/Commerical_Induction' :
                                item === 'Infrared Cooktop' ? '/Infrared-CookTop' :
                                  item === "Idli Steamer" ? '/IdlySteamer' :
                                    item === "Dhokla Steamer" ? '/DhoklaSteamer' :
                                      item === "Commercial Food Steamer" ? '/CommercialSteamer' :
                                        item === "Milk Steamer" ? '/MilkSteamer' :
                                          item === "Deep Fryer" ? '/DeepFry' :
                                            item === "Donut Fryer" ? '/DonutFry' :
                                              item === "Pressure Fryer" ? '/PressureFry' :
                                                item === "Grill Chicken Machine" ? '/GrillChicken' :
                                                  item === "Shawarma Machine" ? '/Shawarma' :
                                                    item === "Alfaham Machine" ? '/Alfaham' :
                                                      item === "Barbeque Machine" ? '/Barbeque' :
                                                        '#'}
                        style={{ textDecoration: 'none', color: 'inherit' }}
                      >
                        {item}
                      </Link>
                    </MenuItem>
                  ))}
                </Menu>
              </div>
            ))}
          </Container>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default Navbar;