import React, { useState, useEffect } from 'react';
import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton, IconButton, Badge, styled } from '@mui/material';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import Elec1 from '../../asserts/Kitchen/Electric/download (1).jpeg';
import Elec2 from '../../asserts/Kitchen/Electric/download.jpeg';
import Elec3 from '../../asserts/Kitchen/Electric/images (1).jpeg';
import Elec4 from '../../asserts/Kitchen/Electric/images.jpeg';

// Styled Badge component for the cart icon
// const StyledBadge = styled(Badge)(({ theme }) => ({
//   '& .MuiBadge-dot': {
//     backgroundColor: theme.palette.secondary.main,
//   },
// }));

// Sample product data for Kitchen Equipment
const products = [
  {
    name: "Idli Steamer",
    imageUrl: Elec1,
    description: "Efficient and durable idli steamer for commercial kitchens.",
    price: "$199.99",
  },
  {
    name: "Dhokla Steamer",
    imageUrl: Elec2,
    description: "High-quality dhokla steamer for perfect steaming every time.",
    price: "$249.99",
  },
  {
    name: "Commercial Steamer",
    imageUrl: Elec3,
    description: "Heavy-duty food steamer for large scale commercial use.",
    price: "$999.99",
  },
  {
    name: "Milk Steamer",
    imageUrl: Elec4,
    description: "Milk steamer for creating creamy froth and smooth milk for beverages.",
    price: "$149.99",
  },
];

function Commercial_Electric() {
  const [loading, setLoading] = useState(true);

  // Simulate loading effect
  useEffect(() => {
    const timer = setTimeout(() => setLoading(false), 1000); // Loading for 1.5 seconds
    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="kitchen-container" style={{ padding: '20px' }}>
      <Typography variant="h3" gutterBottom align="center" style={{ color: '#333' }}>
        Electric Range
      </Typography>

      {/* Sticky Cart Icon */}
      <IconButton
        aria-label="cart"
        style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          zIndex: 9999,
        }}
      >
       
      </IconButton> 

      {/* Product Grid */}
      <Grid container spacing={4} justifyContent="center">
        {products.map((product, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card
              sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
                borderRadius: '10px',
                boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.1)',
                transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
                border: '2px solid #ccc',
                '&:hover': {
                  transform: 'scale(1.05)',
                  borderColor: '#007BFF',
                },
              }}
            >
              {/* Image Skeleton Loader */}
              {loading ? (
                <Skeleton variant="rectangular" width="100%" height={180} />
              ) : (
                <CardMedia
                  component="img"
                  height="180"
                  image={product.imageUrl}
                  alt={product.name}
                  sx={{ objectFit: 'contain' }}
                />
              )}

              <CardContent>
                {/* Text Skeleton Loader */}
                {loading ? (
                  <>
                    <Skeleton variant="text" width="80%" />
                    <Skeleton variant="text" width="60%" />
                    <Skeleton variant="text" width="40%" />
                  </>
                ) : (
                  <>
                    <Typography
                      variant="h6"
                      component="div"
                      sx={{
                        textAlign: 'center',
                        padding: 1,
                        color: '#555',
                        fontWeight: 'bold',
                      }}
                    >
                      {product.name}
                    </Typography>
                    <Typography variant="body2" color="text.secondary" paragraph>
                      {product.description}
                    </Typography>
                    <Typography variant="h6" color="primary">
                      {product.price}
                    </Typography>
                  </>
                )}

                {/* Add to Cart Button with Outlined Variant */}
                <Button variant="outlined" color="primary" fullWidth style={{ marginTop: '10px' }}>
                  {loading ? <Skeleton variant="text" width="60%" /> : "Add to Cart"}
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default Commercial_Electric;
