import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton } from '@mui/material';
import React, { useState, useEffect } from 'react';
import img1 from '../../asserts/Food/Dokla/JSRT20211545-watmrkA.webp';
import img2 from '../../asserts/Food/Dokla/Shriram_Associate_Dhokla_Maker_Manufacturer_1.jpg';
import img3 from '../../asserts/Food/Dokla/images.jpeg';
import img4 from '../../asserts/Food/Dokla/large-commercial-Aluminum-Idli-Steamer-for-restaurant-nishi_826x700.webp';

// Sample Products data for Dhokla Steamer
const Products = [
  {
    name: 'Dhokla Steamer 1',
    imageUrl: img1,
    description: 'Efficient Dhokla Steamer for perfect cooking every time.',
    price: '$1000',
  },
  {
    name: 'Dhokla Steamer 2',
    imageUrl: img2,
    description: 'Commercial-grade Dhokla Steamer with multi-tier trays.',
    price: '$1500',
  },
  {
    name: 'Dhokla Steamer 3',
    imageUrl: img3,
    description: 'Durable steamer for restaurant-grade Dhokla preparation.',
    price: '$1200',
  },
  {
    name: 'Dhokla Steamer 4',
    imageUrl: img4,
    description: 'Large aluminum steamer for bulk cooking.',
    price: '$1800',
  },
];

function DhoklaSteamer() {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setLoading(false); // Simulate loading delay
    }, 1000); // 2 seconds delay

    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="food-preparation">
      <Typography variant="h3" gutterBottom>
        Food Preparation
      </Typography>

      {/* Sticky Cart Icon */}
      <div
        style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          backgroundColor: '#007BFF',
          color: 'white',
          padding: '10px',
          borderRadius: '50%',
          boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
        }}
      >
        
      </div>

      <Grid container spacing={4} justifyContent="center">
        {Products.map((product, index) => (
          <Grid item xs={12} sm={6} md={3} key={index}>
            <Card
              sx={{
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'space-between',
                borderRadius: '10px',
                boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
                transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
                border: '2px solid #ccc',
                '&:hover': {
                  transform: 'scale(1.05)',
                  borderColor: '#007BFF',
                },
              }}
            >
              {loading ? (
                <Skeleton variant="rectangular" width="100%" height={180} />
              ) : (
                <CardMedia
                  component="img"
                  height="180"
                  image={product.imageUrl}
                  alt={product.name}
                  sx={{ objectFit: 'contain' }}
                />
              )}
              <CardContent>
                {loading ? (
                  <Skeleton variant="text" width="80%" />
                ) : (
                  <Typography
                    variant="h6"
                    component="div"
                    sx={{
                      textAlign: 'center',
                      padding: 1,
                      color: '#555',
                      fontWeight: 'bold',
                    }}
                  >
                    {product.name}
                  </Typography>
                )}

                {loading ? (
                  <Skeleton variant="text" width="60%" />
                ) : (
                  <Typography variant="body2" color="text.secondary" paragraph>
                    {product.description}
                  </Typography>
                )}

                {loading ? (
                  <Skeleton variant="text" width="40%" />
                ) : (
                  <Typography variant="h6" color="primary" sx={{ textAlign: 'center' }}>
                    {product.price}
                  </Typography>
                )}

                <Button variant="outlined" color="primary" fullWidth>
                  {loading ? <Skeleton width="100%" height={36} /> : 'Add to Cart'}
                </Button>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>
    </div>
  );
}

export default DhoklaSteamer;
