import React, { useState, useEffect } from 'react';
import { Grid, Card, CardContent, CardMedia, Typography, Button, Skeleton } from '@mui/material';
import img1 from '../../asserts/Bakery/Donut Fryer/Automatic-Donut-Making-Machine-1.webp';
import img2 from '../../asserts/Bakery/Donut Fryer/images (2).jpeg';
import img3 from '../../asserts/Bakery/Donut Fryer/donut-fryer.jpg';

// Sample product data for Donutfry
const products = [
    {
        name: "Electric Donut Fryer Shallow",
        imageUrl: img1,
        description: "",
        price: "Rs:2000"
    },
    {
        name: "Idle steamer 8 Tray",
        imageUrl: img2,
        description: "Donut Fryer",
        price: "1000"
    },
    {
        name: "Donut Fryer automatic",
        imageUrl: img3,
        description: "",
        price: "Rs:500"
    },
];

function DonutFry() {
    const [loading, setLoading] = useState(true);

    // Simulate loading state for demonstration
    useEffect(() => {
        const timer = setTimeout(() => {
            setLoading(false); // Stop loading after 2 seconds
        }, 1000);

        return () => clearTimeout(timer); // Cleanup the timer
    }, []);

    return (
        <div className="food-preparation">
            <Typography variant="h3" gutterBottom>
                Bakery Equipment
            </Typography>

            {/* Sticky Cart Icon */}
            <div
                style={{
                    position: 'fixed',
                    top: '20px',
                    right: '20px',
                    backgroundColor: '#007BFF',
                    color: 'white',
                    padding: '10px',
                    borderRadius: '50%',
                    boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
                }}
            >
                
            </div>

            <Grid container spacing={4} justifyContent="center">
                {products.map((product, index) => (
                    <Grid item xs={12} sm={6} md={3} key={index}>
                        <Card
                            sx={{
                                height: '100%',
                                display: 'flex',
                                flexDirection: 'column',
                                justifyContent: 'space-between',
                                borderRadius: '10px',
                                boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
                                transition: 'transform 0.3s ease-in-out, border-color 0.3s ease-in-out',
                                border: '2px solid #ccc',
                                '&:hover': {
                                    transform: 'scale(1.05)',
                                    borderColor: '#007BFF',
                                },
                            }}
                        >
                            {/* Skeleton loader for image */}
                            {loading ? (
                                <Skeleton variant="rectangular" width="100%" height={180} />
                            ) : (
                                <CardMedia
                                    component="img"
                                    height="180"
                                    image={product.imageUrl}
                                    alt={product.name}
                                    sx={{ objectFit: 'contain' }}
                                />
                            )}

                            <CardContent>
                                {/* Skeleton loader for title */}
                                {loading ? (
                                    <Skeleton variant="text" width="80%" />
                                ) : (
                                    <Typography variant="h6" component="div">
                                        {product.name}
                                    </Typography>
                                )}

                                {/* Skeleton loader for description */}
                                {loading ? (
                                    <Skeleton variant="text" width="60%" />
                                ) : (
                                    <Typography variant="body2" color="text.secondary">
                                        {product.description}
                                    </Typography>
                                )}

                                {/* Skeleton loader for price */}
                                {loading ? (
                                    <Skeleton variant="text" width="40%" />
                                ) : (
                                    <Typography variant="h6" component="div">
                                        {product.price}
                                    </Typography>
                                )}

                                {/* Skeleton loader for button */}
                                {loading ? (
                                    <Skeleton variant="rectangular" width="100%" height={36} />
                                ) : (
                                    <Button variant="outlined" color="primary" fullWidth>
                                        Add to Cart
                                    </Button>
                                )}
                            </CardContent>
                        </Card>
                    </Grid>
                ))}
            </Grid>
        </div>
    );
}

export default DonutFry;
